body {
    background-color: #0d1117;
    margin: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: #c9d1d9;
}

.focus-card {
    background: #161b22;
    box-shadow: 0 8px 32px rgb(12 17 22 / 0.6);
    border-radius: 22px;
    padding: 38px 30px;
    margin: 40px auto;
    max-width: 360px;
    transition: box-shadow 0.3s;
}

.focus-card:hover {
    box-shadow: 0 12px 38px rgb(10 25 40 / 0.8);
}

.focus-title {
    font-weight: 700;
    font-size: 2rem;
    color: #58a6ff;
    text-align: center;
    margin-bottom: 24px;
    text-shadow: 0 1px 2px #11416655;
}

.focus-box {
    padding: 8px 0px 0px 0px;
}

.focus-label {
    font-size: 1.1rem;
    font-weight: 600;
    text-align: center;
    color: #79c0ff;
    margin-bottom: 12px;
}

.mode-row {
    display: flex;
    justify-content: center;
    gap: 16px;
    margin-bottom: 20px;
    flex-wrap: wrap;
}

.option-btn {
    background-color: #161b22;
    border: 1.8px solid #2381ff;
    color: #79c0ff;
    font-weight: 600;
    font-size: 1.04rem;
    padding: 9px 18px;
    border-radius: 12px;
    cursor: pointer;
    transition: background-color 0.18s, border-color 0.18s, color 0.18s, transform 0.17s;
    box-shadow: none;
    /* sin ne√≥n */
}

.option-btn.selected,
.option-btn:hover {
    background-color: #237fff;
    color: #0d1117;
    border-color: #79c0ff;
    transform: scale(1.04);
    box-shadow: 0 2px 6px #14489233;
    /* sombra muy suave y difusa */
}

.input-row {
    display: flex;
    gap: 32px;
    justify-content: center;
    margin-bottom: 22px;
    margin-top: 16px;
}

.input-block {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.input-label {
    font-size: 1.08rem;
    font-weight: 600;
    color: #87bffd;
    margin-bottom: 4px;
}

.input-minutes {
    width: 60px;
    text-align: center;
    padding: 6px;
    margin-bottom: 2px;
    border-radius: 10px;
    border: 2px solid #2362a0;
    font-size: 1.15rem;
    background: #111c28;
    color: #badaff;
}

.input-units {
    font-size: 1rem;
    color: #57aee2;
    margin-top: 2px;
}


.input-minutes:focus {
    border-color: #58a6ff;
}

.minutos-totales-row {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
    margin-top: 18px;
    margin-bottom: 12px;
    width: 100%;
}

.minutos-txt {
    font-size: 1.09rem;
    color: #e6edfa;
    margin-left: 2px;
}


.session-info {
    font-size: 1.1rem;
    font-weight: 600;
    margin-top: 16px;
    text-align: center;
    color: #79c0ff;
    text-shadow: 0 0 8px #1c47d5aa;
    min-height: 30px;
}

.svg-taza {
    display: block;
    margin: 30px auto 20px auto;
    filter: drop-shadow(0 0 10px #58a6ff55);
}

.timer {
    font-family: 'JetBrains Mono', monospace;
    font-weight: 700;
    font-size: 2.8rem;
    color: #58a6ff;
    letter-spacing: 2px;
    text-align: center;
    margin-bottom: 24px;
    text-shadow: 0 0 8px #58a6ff88;
}

.btn-timer {
    background: linear-gradient(90deg, #176EDC 0%, #2980b9 100%);
    color: #dde6f7;
    font-weight: 700;
    font-size: 1.15rem;
    padding: 12px 28px;
    margin: 8px 14px 0 0;
    border-radius: 20px;
    border: none;
    box-shadow: 0 2px 9px #176EDC22;
    cursor: pointer;
    outline: none;
    transform: scale(1);
    transition:
        background 220ms cubic-bezier(.23, 1.45, .2, 1),
        box-shadow 230ms cubic-bezier(.42, 0, .58, 1),
        transform 150ms cubic-bezier(.29, 1.65, .26, .85);
    /* Las curves cubic-bezier dan animaciones suaves y agradables */
}

.timer-btn-row {
    display: flex;
    justify-content: center;
    gap: 24px;
    margin-top: 12px;
}


.btn-timer:hover,
.btn-timer:focus {
    background: linear-gradient(90deg, #237fff 0%, #58a6ff 100%);
    color: #ffffff;
    box-shadow: 0 6px 12px #176EDC50;
    transform: scale(1.02) translateY(-2px);
}

.transition-message {
    text-align: center;
    font-size: 1.44rem;
    font-weight: 700;
    margin: 16px auto 8px auto;
    color: #fee889;
    text-shadow: 0 2px 10px #342f15;
    opacity: 1;
    animation: fadeDropSmooth 2.4s forwards;
}

@keyframes fadeDropSmooth {
    0% {
        opacity: 0;
        transform: translateY(-12px);
    }

    10% {
        opacity: 1;
        transform: translateY(0);
    }

    83% {
        opacity: 1;
    }

    100% {
        opacity: 0;
        transform: translateY(26px);
    }
}


.focus-modal-bg {
    position: fixed;
    z-index: 10000;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(22, 24, 30, 0.86);
    display: flex;
    align-items: center;
    justify-content: center;
    animation: modalFadeIn 0.45s cubic-bezier(.64, .06, .17, .99);
}

@keyframes modalFadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.focus-modal {
    background: #18253a;
    color: #e6eefd;
    border-radius: 24px;
    box-shadow: 0 6px 40px #25365984;
    padding: 38px 32px 28px 32px;
    text-align: center;
    min-width: 280px;
    animation: modalPopup 0.5s;
}

@keyframes modalPopup {
    0% {
        transform: scale(0.83) translateY(-80px);
    }

    60% {
        transform: scale(1.09) translateY(10px);
    }

    100% {
        transform: scale(1) translateY(0);
    }
}

.focus-modal h2 {
    font-size: 1.55rem;
    color: #90e0fe;
    margin-bottom: 12px;
    letter-spacing: 1px;
}

.focus-modal p {
    font-size: 1.13rem;
    margin-bottom: 22px;
    color: #fff8e1;
}

.modal-btn {
    padding: 10px 33px;
    font-size: 1.12rem;
    font-weight: 600;
    border-radius: 12px;
    border: none;
    background: linear-gradient(90deg, #2381ff, #58a6ff 85%);
    color: #1a242d;
    cursor: pointer;
    box-shadow: 0 2px 8px #1c4d916b;
    transition: background .15s, box-shadow .15s;
}

.modal-btn:hover {
    background: linear-gradient(90deg, #1c6ed6, #5ecffd 77%);
    color: #16202b;
}




@media screen and (max-width: 480px) {
    .focus-card {
        max-width: 90vw;
        padding: 24px 20px;
    }

    .mode-row {
        gap: 10px;
    }

    .option-btn {
        font-size: 0.95rem;
        padding: 9px 18px;
    }

    .input-minutes {
        width: 65px;
        font-size: 1rem;
        padding: 7px 10px;
    }

    .timer {
        font-size: 2.4rem;
    }
}